const initState = {
 
}

const orderReducer = (state= initState, action) => {
    // eslint-disable-next-line default-case
    switch(action.type){
         case 'CREATE_ORDER':
             console.log('Utworzono Zamwienie :D ', action.order);
             alert('Dziekujemy za zoenie Zamwienia :)');
             return state
         case 'CREATE_ORDER_ERROR':
             console.log('Utowrzono Zamwienie Bdnie :(', action.error);
             return state
         default:
            return state
    }
   
}

export default orderReducer 